/*//////////////////////////////////////////////////////////////////
    [ Panel ]*/

@mixin ah {
    .ah {
        @content;
    }
}

@mixin pn {
    .pin {
        @content;
    }
}

@include ah {
    width: 100%;
    margin: 0 auto;

    @include ah {
        &-panel {
            width: 100%;
            min-height: 100vh;
            display: -webkit-box;
            display: -webkit-flex;
            display: -moz-box;
            display: -ms-flexbox;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            /* align-items: center; */
            /* padding: 15px; */
            background: rgb(14, 109, 158);
            background: linear-gradient(180deg, rgba(14, 109, 158, 1) 0%, rgba(59, 172, 214, 1) 100%);
        }
        &-wrap {
            width: 390px;
            background: rgb(14, 109, 158);
            background: linear-gradient(180deg, rgba(14, 109, 158, 1) 0%, rgba(59, 172, 214, 1) 100%);
            /* border-radius: 10px; */
            overflow: hidden;
            padding: 77px 55px 33px 55px;
            box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.1);
            -moz-box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.1);
            -webkit-box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.1);
            -o-box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.1);
            -ms-box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.1);
            form {
                width: 100%;
            }
        }
    }
}

@include pn {
    width: 100%;
    &-title {
        display: block;
        font-family: Poppins-Bold;
        font-size: 30px;
        color: #eeab77;
        line-height: 1.2;
        text-align: center;
        margin-top: 7rem;
    }
    &-code {
        display: flex !important;
        -ms-flex-pack: center !important;
        justify-content: center !important;
        .items {
            background: #69a9cf;
            border-radius: 6px;
            margin-left: 0.5rem !important;
            width: 45px;
            height: 44px;
            align-content: center;
            justify-content: center;
            display: grid;
            font-size: 1.2rem;
            font-weight: bold;
            color: #1c627c;
        }
    }
    &-number {
        .group-items {
            display: flex !important;
            -ms-flex-pack: center !important;
            justify-content: center !important;
            .items {
                margin-right: 0.5rem !important;
                margin-left: 0.5rem !important;
                margin-bottom: 0.5rem !important;
                color: #69a9cf;
                text-align: center;
                border-radius: 50%;
                font-size: 2.5rem;
                font-weight: 700;
                box-shadow: none !important;
                i {
                    font-size: 1.8rem;
                }
            }
        }
    }
}
